import React from "react";
import {
  Box,
  Button,
  InputBase,
  IconButton,
  Menu,
  MenuItem,
  Typography,
  Container,
} from "@mui/material";
import { Search, ArrowDropDown, Spa } from "@mui/icons-material";
import NavBar from "./NavBar";

const Header: React.FC = () => {
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);

  const handleMenuClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
  };

  return (
    <Box className="bg-white shadow-md">
      <Container maxWidth={"xl"}>
        <Box className="flex flex-col lg:flex-row items-center justify-between py-4">
          {/* Left Section - Logo */}
          <Box className="flex items-center mb-4 lg:mb-0">
            <Box className="flex items-center gap-4">
              <Box className="bg-[#00670c] p-2 rounded">
                <Spa className="text-white text-2xl" />
              </Box>
              <Typography
                variant="h5"
                className="font-bold text-[#00670c] ml-2"
              >
                GoSellar
              </Typography>
            </Box>
          </Box>

          {/* Middle Section - Search Bar */}
          <Box className="flex items-center w-full lg:w-1/2 mb-4 lg:mb-0">
            <Box className="flex border rounded w-full">
              <Button
                className="flex items-center px-4 border-r max-w-[185px] w-full text-[#00670c]"
                aria-controls="category-menu"
                aria-haspopup="true"
                onClick={handleMenuClick}
                endIcon={<ArrowDropDown />}
              >
                All Categories
              </Button>
              <Menu
                id="category-menu"
                anchorEl={anchorEl}
                open={Boolean(anchorEl)}
                onClose={handleMenuClose}
              >
                <MenuItem onClick={handleMenuClose}>Category 1</MenuItem>
                <MenuItem onClick={handleMenuClose}>Category 2</MenuItem>
              </Menu>
              <InputBase
                placeholder="Search for items..."
                className="w-full px-4 py-2 outline-none"
              />
              <IconButton
                type="submit"
                className="bg-[#00670c] text-white px-4 rounded-none"
              >
                <Search />
              </IconButton>
            </Box>
          </Box>

          {/* Right Section - Promo Button */}
          <NavBar/>
        </Box>
      </Container>
    </Box>
  );
};

export default Header;
